    },
    // 分页
    contentPageStyleChange(){
      let arr = []

      if(this.contents.pageTotal) arr.push('total')
      if(this.contents.pageSizes) arr.push('sizes')
      if(this.contents.pagePrevNext){
        arr.push('prev')
        if(this.contents.pagePager) arr.push('pager')
        arr.push('next')
      }
      if(this.contents.pageJumper) arr.push('jumper')
      this.layouts = arr.join()
      this.contents.pageEachNum = 10
    },

            ruzhudengjiCrossAddOrUpdateHandler(row,type){
      this.showFlag = false;
      this.addOrUpdateFlag = false;
      this.ruzhudengjiCrossAddOrUpdateFlag = true;
      this.$storage.set('crossObj',row);
      this.$storage.set('crossTable','kefangyuding');
      this.$nextTick(() => {
        this.$refs.ruzhudengjiCrossaddOrUpdate.init(row.id,type);
      });
    },
                payHandler(row){
      this.$storage.set('paytable','kefangyuding');
      this.$storage.set('payObject',row);
      this.$router.push('pay');
    },
            chartDialog() {
      this.chartVisiable = !this.chartVisiable;
      this.$nextTick(()=>{
                                                         // kefangmingcheng kefangmingcheng
                                                                                                                                                                                               //  zongjiage
                                                                                                                                                                                                                                                                          // xcolumn ycolumn kefangmingcheng zongjiage
                var zongjiageChart = this.$echarts.init(document.getElementById("zongjiageChart"),'macarons');
        this.$http({
            url: `value/kefangyuding/kefangmingcheng/zongjiage`,
            method: "get"
        }).then(({ data }) => {
            if (data && data.code === 0) {
                let res = data.data;
                let xAxis = [];
                let yAxis = [];
                let pArray = []
                for(let i=0;i<res.length;i++){
                    xAxis.push(res[i].kefangmingcheng);
                    yAxis.push(res[i].total);
                    pArray.push({
                    value: res[i].total,
                    name: res[i].kefangmingcheng
                    })
                    var option = {};
                                        option = {
                        tooltip: {
                          trigger: 'item',
                          formatter: '{b} : {c}'
                        },
                        title: {
                            text: '客房预订',
                            left: 'center'
                        },
                        xAxis: {
                            type: 'category',
                            data: xAxis
                        },
                        yAxis: {
                            type: 'value'
                        },
                        series: [{
                            data: yAxis,
                            type: 'bar'
                        }]
                    };
                                        // 使用刚指定的配置项和数据显示图表。
                    zongjiageChart.setOption(option);
                    //根据窗口的大小变动图表
                    window.onresize = function() {
                        zongjiageChart.resize();
                    };
                }
            }
        });
              })
    },
        init () {
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                            },
    search() {
      this.pageIndex = 1;
      this.getDataList();
    },
    // 获取数据列表
    getDataList() {
      this.dataListLoading = true;
      let params = {
        page: this.pageIndex,
        limit: this.pageSize,
        sort: 'id',
      }
                                                                if(this.searchForm.kefangmingcheng!='' && this.searchForm.kefangmingcheng!=undefined){
            params['kefangmingcheng'] = '%' + this.searchForm.kefangmingcheng + '%'
          }
                                                            if(this.searchForm.kefangleixing!='' && this.searchForm.kefangleixing!=undefined){
            params['kefangleixing'] = '%' + this.searchForm.kefangleixing + '%'
          }
                                                                                  if(this.searchForm.jiudianmingcheng!='' && this.searchForm.jiudianmingcheng!=undefined){
            params['jiudianmingcheng'] = '%' + this.searchForm.jiudianmingcheng + '%'
          }
                                                                                                                                                                                                                      if(this.searchForm.yonghuxingming!='' && this.searchForm.yonghuxingming!=undefined){
            params['yonghuxingming'] = '%' + this.searchForm.yonghuxingming + '%'
          }
                                                                                                                                            this.$http({
        url: "kefangyuding/page",
        method: "get",
        params: params
      }).then(({ data }) => {
        if (data && data.code === 0) {
          this.dataList = data.data.list;
          this.totalPage = data.data.total;
        } else {
          this.dataList = [];
          this.totalPage = 0;
        }
        this.dataListLoading = false;
      });
    },
    // 每页数
    sizeChangeHandle(val) {
      this.pageSize = val;
      this.pageIndex = 1;
      this.getDataList();
    },
    // 当前页
    currentChangeHandle(val) {
      this.pageIndex = val;
      this.getDataList();
    },
    // 多选
    selectionChangeHandler(val) {
      this.dataListSelections = val;
    },
    // 添加/修改
    addOrUpdateHandler(id,type) {
      this.showFlag = false;
      this.addOrUpdateFlag = true;
      this.crossAddOrUpdateFlag = false;
      if(type!='info'){
        type = 'else';
      }
      this.$nextTick(() => {
        this.$refs.addOrUpdate.init(id,type);
      });
    },
    // 查看评论
        // 审核窗口
    shDialog(row){
      this.sfshVisiable = !this.sfshVisiable;
      if(row){
        this.shForm = {
                    yudingbianhao: row.yudingbianhao,
                    kefangmingcheng: row.kefangmingcheng,
                    kefangleixing: row.kefangleixing,
                    kefangtupian: row.kefangtupian,
                    jiudianmingcheng: row.jiudianmingcheng,
                    jiudiandizhi: row.jiudiandizhi,
                    kefangjiage: row.kefangjiage,
                    shuliang: row.shuliang,
                    zongjiage: row.zongjiage,
                    ruzhurenshu: row.ruzhurenshu,
                    xiadanshijian: row.xiadanshijian,
                    yonghuzhanghao: row.yonghuzhanghao,
                    yonghuxingming: row.yonghuxingming,
                    lianxidianhua: row.lianxidianhua,
                    beizhu: row.beizhu,
                    sfsh: row.sfsh,
                    shhf: row.shhf,
                    ispay: row.ispay,
                    id: row.id
        }
      }
    },
    // 审核
    shHandler(){
      this.$confirm(`确定操作?`, "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(() => {
        this.$http({
          url: "kefangyuding/update",
          method: "post",
          data: this.shForm
        }).then(({ data }) => {
          if (data && data.code === 0) {
            this.$message({
              message: "操作成功",
              type: "success",
              duration: 1500,
              onClose: () => {
                this.getDataList();
                this.shDialog()
              }
            });
          } else {
            this.$message.error(data.msg);
          }
        });
      });
    },
        // 下载
    download(file){
      window.open(`${file}`)
    },
    // 删除
    deleteHandler(id) {
      var ids = id
        ? [Number(id)]
        : this.dataListSelections.map(item => {
            return Number(item.id);
          });
      this.$confirm(`确定进行[${id ? "删除" : "批量删除"}]操作?`, "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(() => {
        this.$http({
          url: "kefangyuding/delete",
          method: "post",
          data: ids
        }).then(({ data }) => {
          if (data && data.code === 0) {
            this.$message({
              message: "操作成功",
              type: "success",
              duration: 1500,
              onClose: () => {
                this.search();
              }
            });
          } else {
            this.$message.error(data.msg);
          }
        });
      });
    },
  }

};
